import { writable } from "svelte/store";
export const wordmaps = writable([]);
export const currentWordmap = writable({});
export function setCurrentWordmap(uuid) {
    wordmaps.subscribe(wordmaps => {
        for (const wordmap of wordmaps) {
            if (wordmap.id === uuid) {
                currentWordmap.set(wordmap);
            }
        }
    });
}
